@import "https://fonts.googleapis.com/css2?family=Akshar&family=Indie+Flower&family=Ms+Madi&display=swap";
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #5a6161;
}

.titulo, h1 {
  text-align: center;
  color: #fff;
  font-family: Ms Madi, cursive;
  font-size: 4em;
}

.subtitulo, h2 {
  color: #fff;
  font-family: Ms Madi, cursive;
  font-size: 2em;
}

.texto {
  color: #000;
  font-family: Akshar, sans-serif;
  font-size: large;
}

figcaption {
  font-family: Indie Flower, cursive;
}

@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(.1);
  }

  60% {
    opacity: 1;
    transform: scale(1.2);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes giros {
  0% {
    opacity: 0;
    transform: translateX(0)rotate(0deg);
  }

  25% {
    opacity: .5;
    transform: translateX(25vw)rotate(0deg);
  }

  40% {
    opacity: .2;
    transform: translateX(25vw)translateY(25vh)rotate(75deg);
  }

  65% {
    opacity: .6;
    transform: translateX(75vw)translateY(60vh)rotate(-25deg);
  }

  85% {
    opacity: .2;
    transform: translateX(25vw)translateY(40vh)rotate(-25deg);
  }

  100% {
    opacity: .5;
  }
}

@keyframes tamaño {
  0% {
    opacity: 1;
    width: 50%;
  }

  50% {
    opacity: .2;
    width: 40%;
  }

  100% {
    opacity: 1;
    width: 50%;
  }
}

article {
  margin-left: 5em;
  margin-right: em;
}

@media only screen and (min-width: 768px) {
  article {
    margin-left: 15%;
    margin-right: 15%;
  }
}

